html{
	height: 100vh;
	font-family: "Helvetica Neue", Arial, sans-serif;
}

body{
	
	margin:0;
	display: flex;
	justify-content: center;
	align-items: center;
	width: 100%;
	height: 100%;
	background: linear-gradient(45deg, #000122, #000255, #831F56, #1D002B, #40005E);
	background-size: 400% 100%;
	animation: gradient 45s linear infinite;
	animation-direction: alternate;
}

@keyframes gradient{
	0% {background-position: 0%}
	100% {background-position: 100%}
}

.container{
	display: grid;
	grid-template-rows: 2fr 1fr;
	grid-template-columns: 1fr 1fr;
	width: 80vw;
	height: 80vh;
}

#time-display-container{
	grid-column-start: 1;
	grid-column-end: 3;
	grid-row-start: 1;
	grid-row-end: 2;
}

.sub-container{
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	font-size: 2rem;
	color: white;
}

.sub-sub-container{
	display: flex;
}

.min-div{
	margin-left: 0.5rem;
}

#time-left{
	font-size: 7rem;
}

i{
	color: white;
	font-size: 1.5rem;
	padding-left: 0.5rem;
	padding-right: 0.5rem;
	transition: 0.1s;
}

i:hover{
	transform: scale(1.3);
}

.clickActive{
	color: black;

}

.blink{
	color: black;
	
}





